.box {
	max-width: $containerWidth;
	width: 100%;
	margin: 0 auto;
	@include desktop-md {
		padding-left: 15px;
		padding-right: 15px;
	}
}

.row {
	display: flex;
	flex-wrap: wrap;
	margin-left: -0.8125rem;
	margin-right: -0.8125rem;
}

.row [class*='col-'] {
	padding-left: 0.8125rem;
	padding-right: 0.8125rem;
}

.row.no_gutter {
	margin-left: 0;
	margin-right: 0;
}

.row.no_gutter [class*='col-'] {
	padding-left: 0;
	padding-right: 0;
}

.col-8 {
	width: 66.66666666666666%;
	@include tablet-md-1024 {
		width: 50%;
	}
	@include tablet-sm-768 {
		width: 100%;
	}
}


.col-6 {
	width: 50%;
	@include tablet-md-1024 {
		width: 100%;
	}
}

.col-4 {
	width: 33.3333333%;
	@include tablet-md-1024 {
		width: 50%;
	}
	@include tablet-sm-768 {
		width: 100%;
	}
}

.col-3 {
	width: 25%;
	@include tablet-md-1024 {
		width: 33.33333%;
	}
	@include tablet-sm-768 {
		width: 50%;
	}
	@include mobile-sm-480 {
		width: 100%;
	}
}

body {
	font-family: Lato, sans-serif;
	font-weight: 300;
	color: #3a3a3a;
	background: rgb(242, 242, 242);
	background: linear-gradient(90deg, rgba(242, 242, 242, 1) 21%, rgba(228, 226, 226, 1) 27%, rgba(214, 214, 214, 1) 34%);
}

header {
	padding: 30px 0;

	.logo {
		display: flex;
		align-items: center;
		justify-content: center;

		a {
			font-weight: bold;
			font-size: 48px;
			line-height: 1.2;
			text-align: center;
			color: rgba(0, 0, 0, 0.5);
			@include tablet-md-1024 {
				font-size: 32px;
			}
			@include tablet-sm-768 {
				text-align: left;
			}
		}
	}

	.main_menu {
		margin-top: 30px;
		@include tablet-sm-768 {
			opacity: 0;
			pointer-events: none;
			transition: .3s;
			transform: translateX(-100%);
			position: fixed;
			left: 0;
			top: 0;
			width: 100%;
			height: 100vh;
			background: #fff;
			z-index: 99;
			margin-top: 0;

			&.active {
				opacity: 1;
				pointer-events: visible;
				transform: translateX(0);
			}
		}

		ul {
			display: flex;
			align-items: center;
			justify-content: center;
			@include tablet-sm-768 {
				display: flex;
				flex-direction: column;
				justify-content: flex-start;
				overflow-y: auto;
				overflow-x: hidden;
				width: 100%;
				height: 100%;
				padding: 150px 0 30px 0;
				@media screen and (orientation:landscape) {
					justify-content: center;
					padding: 30px 0;
				}
			}

			li {
				list-style: none;
				margin-right: 100px;
				@include tablet-md-1024 {
					margin-right: 50px;
				}
				@include tablet-sm-768 {
					margin-right: 0;
					margin-bottom: 30px;
				}

				a {
					display: flex;
					align-items: center;
					justify-content: center;
					text-align: center;
					padding: 0 15px;
					font-weight: 600;
					font-size: 18px;
					color: #FFFFFF;
					background: #ED7844;
					border: 1px solid #ED7844;
					border-radius: 7px;
					height: 42px;
					transition: .3s;

					&:hover {
						background: transparent;
						color: #ED7844;
					}
				}

				&.active {
					a {
						background: transparent;
						color: #ED7844;
					}
				}

				&:last-child {
					margin-right: 0;
					@include tablet-sm-768 {
						margin-bottom: 0;
					}
				}
			}
		}
	}
}

.section_top {
	height: 500px;
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	display: flex;
	@include tablet-md-1024 {
		height: 300px;
		background-attachment: inherit;
	}
}

.about_us {
	padding: 100px 0;
	@include tablet-md-1024 {
		padding: 50px 0;
	}
}

.about_us, .opening_hours {
	.row {
		align-items: center;
		@include tablet-md-1024 {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	.image {
		@include tablet-md-1024 {
			max-width: 700px;
			margin-left: auto;
			margin-right: auto;
		}
	}

	.content_box {
		padding: 40px;
		background: #fff;
		position: relative;
		left: -100px;
		min-width: 700px;
		min-height: 450px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		@media (max-width: 1300px) {
			min-width: auto;
		}
		@include tablet-md-1024 {
			position: static;
			margin-bottom: 30px;
			padding: 30px;
			min-height: auto;
		}
	}
}

.about_us {
	.row {
		align-items: center;
		@include tablet-md-1024 {
			flex-direction: column-reverse;
		}
	}
}

.title_h2 {
	font-weight: bold;
	font-size: 25px;
	line-height: 1.2;
	color: #000000;
	margin-bottom: 30px;
	@include tablet-sm-768 {
		font-size: 22px;
	}
}

p, ul li {
	font-weight: 500;
	font-size: 18px;
	line-height: 1.4;
	color: #000000;
	margin-bottom: 15px;
}

p, .content_box ul li {
	&:last-child {
		margin-bottom: 0;
	}
}

ul {
	li {
		list-style: none;
	}
}


.opening_hours {
	.content_box {
		left: auto;
		right: -100px;
	}
}

footer {
	margin-top: 100px;
	padding: 30px 0;
	text-align: center;
	background: #fff;
	@include tablet-md-1024 {
		margin-top: 50px;
	}

	p {
		margin-bottom: 0;
	}
}

.boxing_content {
	margin-top: -100px;
	@include tablet-md-1024 {
		margin-top: -50px;
	}

	.inner_box {
		background: #fff;
		padding: 100px;
		@include tablet-md-1024 {
			padding: 30px;
		}
	}
}

.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 0 15px;
	font-weight: 600;
	font-size: 18px;
	color: #FFFFFF;
	background: #ED7844;
	border: 1px solid #ED7844;
	border-radius: 7px;
	height: 42px;
	transition: .3s;

	&:hover {
		background: transparent;
		color: #ED7844;
	}
}

.section_contact {
	padding-top: 100px;
	@include tablet-md-1024 {
		padding-top: 50px;
	}

	.row {
		align-items: center;
		@include tablet-sm-768 {
			flex-direction: column-reverse;
		}
	}

	.wrap_social_btn {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		@include tablet-sm-768 {
			margin-top: 50px;
		}

		.item {
			width: 50%;
			margin-bottom: 50px;
			display: flex;
			justify-content: center;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.form_box {
		padding: 50px;
		background: #fff;
		margin-left: 100px;
		@include desktop-md {
			margin-left: 0;
			padding: 30px;
		}

		form {
			display: flex;
			flex-direction: column;
			align-items: flex-start;

			input[type = submit] {
				min-width: 150px;
			}
		}
	}
}

.input_def {
	width: 100%;
	height: 50px;
	margin-bottom: 20px;
	padding-left: 20px;
	font-size: 16px;
	border: 1px solid #ed7844;
	max-width: 40%;
	@include desktop-md {
		max-width: 100%;
	}
}

textarea.input_def {
	max-width: none;
	resize: none;
	height: 200px;
	padding: 20px;
}

.burger, .close_menu {
	display: none;
	@include tablet-sm-768 {
		display: block;
	}
}

.close_menu {
	position: absolute;
	right: 30px;
	top: 30px;
	width: 30px;
	height: 30px;

	img {
		width: 100%;
		height: 100%;
		display: flex;
	}
}

.burger {
	width: 39px;
	height: 39px;

	img {
		width: 100%;
		height: 100%;
		display: flex;
	}
}

.wrap_burger {
	@include tablet-sm-768 {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
}

.body_overflow {
	overflow: hidden;
}
