* {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  line-height: 1;
  min-width: 320px;
  position: relative;
}

article,
aside,
details,
figure,
figcaption,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

fieldset {
  border: 0;
  padding: 0;
  margin: 0;
  min-width: 0;
}

.hidden,
[hidden],
template {
  display: none;
}

a {
  text-decoration: none;
  background: transparent;
}

a:active, a:hover {
  outline: 0;
}

a img {
  border: 0;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 75%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -.5em;
}

sub {
  bottom: -.25em;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  border: 0;
  font: inherit;
  color: inherit;
  appearance: none;
  border-radius: 0;
  text-transform: none;
  box-sizing: border-box;
  outline: none !important;
  background-clip: padding-box;
  background-color: transparent;
}

button,
html input[type='button'] {
  cursor: pointer;
  appearance: button;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type='reset'], input[type='submit'] {
  cursor: pointer;
  appearance: button;
}

input[type='checkbox'], input[type='radio'] {
  box-sizing: border-box;
  padding: 0;
}

input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button {
  height: auto;
}

input[type='search'] {
  appearance: textfield;
  box-sizing: border-box;
}

input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
  appearance: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  pointer-events: none;
  user-select: none;
  border: 0;
}

.clearfix {
  clear: both;
}

body {
  color: #000;
  font-size: 16px;
  font-family: sans-serif;
}

.box {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

@media (max-width: 1200px) {
  .box {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -0.8125rem;
  margin-right: -0.8125rem;
}

.row [class*='col-'] {
  padding-left: 0.8125rem;
  padding-right: 0.8125rem;
}

.row.no_gutter {
  margin-left: 0;
  margin-right: 0;
}

.row.no_gutter [class*='col-'] {
  padding-left: 0;
  padding-right: 0;
}

.col-8 {
  width: 66.66666666666666%;
}

@media (max-width: 1024px) {
  .col-8 {
    width: 50%;
  }
}

@media (max-width: 768px) {
  .col-8 {
    width: 100%;
  }
}

.col-6 {
  width: 50%;
}

@media (max-width: 1024px) {
  .col-6 {
    width: 100%;
  }
}

.col-4 {
  width: 33.3333333%;
}

@media (max-width: 1024px) {
  .col-4 {
    width: 50%;
  }
}

@media (max-width: 768px) {
  .col-4 {
    width: 100%;
  }
}

.col-3 {
  width: 25%;
}

@media (max-width: 1024px) {
  .col-3 {
    width: 33.33333%;
  }
}

@media (max-width: 768px) {
  .col-3 {
    width: 50%;
  }
}

@media (max-width: 480px) {
  .col-3 {
    width: 100%;
  }
}

body {
  font-family: Lato, sans-serif;
  font-weight: 300;
  color: #3a3a3a;
  background: #f2f2f2;
  background: linear-gradient(90deg, #f2f2f2 21%, #e4e2e2 27%, #d6d6d6 34%);
}

header {
  padding: 30px 0;
}

header .logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

header .logo a {
  font-weight: bold;
  font-size: 48px;
  line-height: 1.2;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
}

@media (max-width: 1024px) {
  header .logo a {
    font-size: 32px;
  }
}

@media (max-width: 768px) {
  header .logo a {
    text-align: left;
  }
}

header .main_menu {
  margin-top: 30px;
}

@media (max-width: 768px) {
  header .main_menu {
    opacity: 0;
    pointer-events: none;
    transition: .3s;
    transform: translateX(-100%);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background: #fff;
    z-index: 99;
    margin-top: 0;
  }
  header .main_menu.active {
    opacity: 1;
    pointer-events: visible;
    transform: translateX(0);
  }
}

header .main_menu ul {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 768px) {
  header .main_menu ul {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
    padding: 150px 0 30px 0;
  }
}

@media screen and (max-width: 768px) and (orientation: landscape) {
  header .main_menu ul {
    justify-content: center;
    padding: 30px 0;
  }
}

header .main_menu ul li {
  list-style: none;
  margin-right: 100px;
}

@media (max-width: 1024px) {
  header .main_menu ul li {
    margin-right: 50px;
  }
}

@media (max-width: 768px) {
  header .main_menu ul li {
    margin-right: 0;
    margin-bottom: 30px;
  }
}

header .main_menu ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 15px;
  font-weight: 600;
  font-size: 18px;
  color: #FFFFFF;
  background: #ED7844;
  border: 1px solid #ED7844;
  border-radius: 7px;
  height: 42px;
  transition: .3s;
}

header .main_menu ul li a:hover {
  background: transparent;
  color: #ED7844;
}

header .main_menu ul li.active a {
  background: transparent;
  color: #ED7844;
}

header .main_menu ul li:last-child {
  margin-right: 0;
}

@media (max-width: 768px) {
  header .main_menu ul li:last-child {
    margin-bottom: 0;
  }
}

.section_top {
  height: 500px;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
}

@media (max-width: 1024px) {
  .section_top {
    height: 300px;
    background-attachment: inherit;
  }
}

.about_us {
  padding: 100px 0;
}

@media (max-width: 1024px) {
  .about_us {
    padding: 50px 0;
  }
}

.about_us .row, .opening_hours .row {
  align-items: center;
}

@media (max-width: 1024px) {
  .about_us .row, .opening_hours .row {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 1024px) {
  .about_us .image, .opening_hours .image {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
  }
}

.about_us .content_box, .opening_hours .content_box {
  padding: 40px;
  background: #fff;
  position: relative;
  left: -100px;
  min-width: 700px;
  min-height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (max-width: 1300px) {
  .about_us .content_box, .opening_hours .content_box {
    min-width: auto;
  }
}

@media (max-width: 1024px) {
  .about_us .content_box, .opening_hours .content_box {
    position: static;
    margin-bottom: 30px;
    padding: 30px;
    min-height: auto;
  }
}

.about_us .row {
  align-items: center;
}

@media (max-width: 1024px) {
  .about_us .row {
    flex-direction: column-reverse;
  }
}

.title_h2 {
  font-weight: bold;
  font-size: 25px;
  line-height: 1.2;
  color: #000000;
  margin-bottom: 30px;
}

@media (max-width: 768px) {
  .title_h2 {
    font-size: 22px;
  }
}

p, ul li {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.4;
  color: #000000;
  margin-bottom: 15px;
}

p:last-child, .content_box ul li:last-child {
  margin-bottom: 0;
}

ul li {
  list-style: none;
}

.opening_hours .content_box {
  left: auto;
  right: -100px;
}

footer {
  margin-top: 100px;
  padding: 30px 0;
  text-align: center;
  background: #fff;
}

@media (max-width: 1024px) {
  footer {
    margin-top: 50px;
  }
}

footer p {
  margin-bottom: 0;
}

.boxing_content {
  margin-top: -100px;
}

@media (max-width: 1024px) {
  .boxing_content {
    margin-top: -50px;
  }
}

.boxing_content .inner_box {
  background: #fff;
  padding: 100px;
}

@media (max-width: 1024px) {
  .boxing_content .inner_box {
    padding: 30px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 15px;
  font-weight: 600;
  font-size: 18px;
  color: #FFFFFF;
  background: #ED7844;
  border: 1px solid #ED7844;
  border-radius: 7px;
  height: 42px;
  transition: .3s;
}

.btn:hover {
  background: transparent;
  color: #ED7844;
}

.section_contact {
  padding-top: 100px;
}

@media (max-width: 1024px) {
  .section_contact {
    padding-top: 50px;
  }
}

.section_contact .row {
  align-items: center;
}

@media (max-width: 768px) {
  .section_contact .row {
    flex-direction: column-reverse;
  }
}

.section_contact .wrap_social_btn {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .section_contact .wrap_social_btn {
    margin-top: 50px;
  }
}

.section_contact .wrap_social_btn .item {
  width: 50%;
  margin-bottom: 50px;
  display: flex;
  justify-content: center;
}

.section_contact .wrap_social_btn .item:last-child {
  margin-bottom: 0;
}

.section_contact .form_box {
  padding: 50px;
  background: #fff;
  margin-left: 100px;
}

@media (max-width: 1200px) {
  .section_contact .form_box {
    margin-left: 0;
    padding: 30px;
  }
}

.section_contact .form_box form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.section_contact .form_box form input[type=submit] {
  min-width: 150px;
}

.input_def {
  width: 100%;
  height: 50px;
  margin-bottom: 20px;
  padding-left: 20px;
  font-size: 16px;
  border: 1px solid #ed7844;
  max-width: 40%;
}

@media (max-width: 1200px) {
  .input_def {
    max-width: 100%;
  }
}

textarea.input_def {
  max-width: none;
  resize: none;
  height: 200px;
  padding: 20px;
}

.burger, .close_menu {
  display: none;
}

@media (max-width: 768px) {
  .burger, .close_menu {
    display: block;
  }
}

.close_menu {
  position: absolute;
  right: 30px;
  top: 30px;
  width: 30px;
  height: 30px;
}

.close_menu img {
  width: 100%;
  height: 100%;
  display: flex;
}

.burger {
  width: 39px;
  height: 39px;
}

.burger img {
  width: 100%;
  height: 100%;
  display: flex;
}

@media (max-width: 768px) {
  .wrap_burger {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.body_overflow {
  overflow: hidden;
}

main {
  position: relative;
  overflow: hidden;
}
